.sidebar{
      display: flex;
      flex-direction: column;
      flex: 0.35;
}
.sidebar_header{
      display: flex;
      justify-content: space-between;
      padding: 20px;
      border-right: 1px solid lightgray;
}
.sidebar_headerRight{
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-width: 10vw;
}
.sidebar_headerRight > .MuiSvgIcon-root{
      margin-right: 2vw;
      font-size: 24px !important;
}
.sidebar_search{
      display: flex;
      align-items: center;
      height: 39px;
      padding: 25px;
      background-color: rgb(216, 208, 208);
}
.sidebar_searchContainer{
      display: flex;
      align-items: center;
      background-color:white ;
      width: 100%;
      height: 35px;
      border-radius: 20px;
}
.sidebar_searchContainer > input{
      border: none;
      margin-left: 10px;
      outline: none;
}
.sidebar_searchContainer >.MuiSvgIcon{
      color: gray;
      padding: 10px;
}
.sidebar_chats{
      background-color: white;
      flex: 1;
      overflow: scroll;
}